@import url("https://fonts.googleapis.com/css?family=Quicksand:300,500");
@import url("https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto:300,700");
@import url("https://fonts.googleapis.com/css?family=Montserrat:900|Work+Sans:300");
@import url("https://fonts.googleapis.com/css?family=Lato:300,700|Prata");
@import url("https://fonts.googleapis.com/css?family=Chivo:300,700|Playfair+Display:700i");
@import url('https://fonts.googleapis.com/css?family=Rajdhani:600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
  font-weight: 300;
  font-size: medium;
}

.text-title {
  font-family: "Rajdhani", sans-serif;
  text-transform: uppercase;
  font-size: x-large;
  font-weight: 600;
  letter-spacing: 0.1em;
}

.text-subtitle {
  font-size: large;
  font-weight: 500;
}

.text-bold {
  font-size: x-large;
  font-weight: 900;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.main-background {
  /* background-image: linear-gradient(to right, #8360c3, #2ebf91); */


 
background-image: linear-gradient(to right, #0f0c29, #302b63, #24243e);
 
}

.App-header {
  background-size: 100% 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

html,
body,
#root,
.App {
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
